#websiteTitle span {
  display: inline-block;
  margin-left: -15px;
  font-size: 15rem;
  line-height: 200px;
  font-family: "Oswald", sans-serif;
  position: relative;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-animation: slide 6s linear infinite;
          animation: slide 6s linear infinite;
  background-size: auto 400px;
  background-position: left 0px;
  background-image: -webkit-linear-gradient(top, #604439 0px, #604439 100px, black 100px, #9e9a75 102px, #9e9a75 130px, black 130px, #1c222e 132px, #1c222e 180px, black 180px, #41533b 182px, #41533b 200px, black 200px, #604439 202px, #604439 240px, black 240px, #9e9a75 242px, #9e9a75 300px, black 300px, #1c222e 302px, #1c222e 350px, black 350px, #41533b 352px, #41533b 398px, black 399px, black 400px);
  background-image: linear-gradient(to bottom, #604439 0px, #604439 100px, black 100px, #9e9a75 102px, #9e9a75 130px, black 130px, #1c222e 132px, #1c222e 180px, black 180px, #41533b 182px, #41533b 200px, black 200px, #604439 202px, #604439 240px, black 240px, #9e9a75 242px, #9e9a75 300px, black 300px, #1c222e 302px, #1c222e 350px, black 350px, #41533b 352px, #41533b 398px, black 399px, black 400px); }
  #websiteTitle span:nth-child(odd) {
    top: 30px;
    background-image: -webkit-linear-gradient(top, #604439 0px, #604439 70px, black 70px, #9e9a75 72px, #9e9a75 100px, black 100px, #1c222e 102px, #1c222e 150px, black 150px, #41533b 152px, #41533b 170px, black 170px, #604439 172px, #604439 210px, black 210px, #9e9a75 212px, #9e9a75 270px, black 270px, #1c222e 272px, #1c222e 320px, black 320px, #41533b 322px, #41533b 370px);
    background-image: linear-gradient(to bottom, #604439 0px, #604439 70px, black 70px, #9e9a75 72px, #9e9a75 100px, black 100px, #1c222e 102px, #1c222e 150px, black 150px, #41533b 152px, #41533b 170px, black 170px, #604439 172px, #604439 210px, black 210px, #9e9a75 212px, #9e9a75 270px, black 270px, #1c222e 272px, #1c222e 320px, black 320px, #41533b 322px, #41533b 370px); }

@-webkit-keyframes slide {
  100% {
    background-position: left -400px; } }

@keyframes slide {
  100% {
    background-position: left -400px; } }

